nav{
    background: rgba(0,0,0,0.3);
    width: max-content;
    display: block;
    padding: 0.7rem  1.7rem;
    z-index: 2;
    position: fixed;
    left: 40%;
    transform: translateX(-50);
    bottom: 2rem;
    display: flex;
    gap:0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);

}

nav a {
    background: transparent;
    padding: 0.8rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;

}
nav a:hover{
    
    background: rgba(255, 246, 246, 0.3);

}
nav a.active{
    background: red;
    color: var(--color-white);
}


@media screen and (max-width :600px) {


        nav{
            display: none;
        }
       

}
